<template>
  <div id="edit-note">
    <head-top title="课件点评">
      <span slot="right" @click="saveData">
        <i class="iconfont icon-finish"></i>
      </span>
    </head-top>

    <div class="main">

      <van-cell-group>
        <van-cell title="课件评分">
          <van-stepper slot="default" v-model="formComment.score" step="1" :max="100" />
        </van-cell>
      </van-cell-group>

      <van-cell-group>
        <van-cell title="匿名评价">
          <van-switch v-model="formComment.anonymous" size="24px" />
        </van-cell>
      </van-cell-group>

      <van-cell-group>
        <van-field v-model="formComment.comment" rows="2" autosize label="课件评价" type="textarea"
          maxlength="500" placeholder="请输入评价" show-word-limit>
        </van-field>
      </van-cell-group>

    </div>
  </div>
</template>

<script>
import { emojValidator } from "@/utils/index";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      formComment: {
        id: "",
        courseID: this.$route.params.courseId,
        coursewareID: this.$route.params.coursewareId,
        comment: "",
        anonymous: false,
        score: 100
      },
      saveLoading: false
    };
  },
  created() {
    this.getData();
  },
  mounted() {},
  methods: {
    getData() {
      let reqData = {
        courseID: this.formComment.courseID,
        studentID: this.$store.getters.studentID,
        cID: this.formComment.coursewareID
      };
      this.$mainHttp.coursewareComment.getCommentData(reqData).then(res => {
        if (res.Result && res.Data) {
          this.formComment.id = res.Data["CCID"];
          this.formComment.comment = res.Data["Comment"];
          this.formComment.score = res.Data["Score"];
          this.formComment.anonymous = res.Data["IsAnonymous"] ? true : false;
        }
      });
    },

    saveData() {
      if (this.saveLoading) return;

      if (!this.formComment.comment) {
        this.$toast("请填写课件评价");
        return;
      } else if (emojValidator(this.formComment.comment)) {
        this.$toast("不支持表情字符");
        return;
      }

      this.saveLoading = true;
      let reqData = {
        ccID: this.formComment.id,
        courseID: this.formComment.courseID,
        cID: this.formComment.coursewareID,
        studentID: this.$store.getters.studentID,
        score: this.formComment.score,
        comment: this.formComment.comment,
        isAnonymous: this.formComment.anonymous ? 1 : 0,
        type: 2
      };
      this.$mainHttp.coursewareComment
        .postCommentSave(reqData)
        .then(res => {
          if (res.Result) {
            this.dialogCommentVisible = false;
            this.$toast.success("提交成功！");
            setTimeout(() => {
              this.$router.push("/space/homework");
            }, 500);
          } else if (res.Info) {
            this.$toast.fail(res.Info);
          }
          this.saveLoading = false;
        })
        .catch(err => {
          this.saveLoading = false;
        });
    }
  }
};
</script>

<style lang='less' scoped>
#edit-note {
  width: 100%;
  height: 100%;
  background: #ebf0ef;
  .icon-finish {
    float: right;
    margin-right: 4vw;
    line-height: 4.3rem;
    color: #fff;
    font-size: 1.5rem;
  }
  .main {
    text-align: left;
    background: #ebf0ef;

    .tt {
      width: 92vw;
      padding-left: 4vw;
      padding-right: 4vw;
      color: #999;
      height: 4rem;
      line-height: 4rem;
      background: #fff;
      input {
        color: #999;
        height: 3.5rem;
        line-height: 3.5rem;
        width: 100%;
      }
    }

    /deep/ .van-dropdown-menu__title {
      width: 87%;
      margin-left: -5%;
      color: rgb(117, 117, 117);
      font-size: 1.2rem;
    }
  }
}
</style>


